import React from 'react';
import { Check, ChevronDown, ChevronUp } from 'lucide-react';
import { useState } from 'react';

export default function Specification({ data }) {
  const specField = data?.find(f => f.name === "specifications");
  const features = specField?.items || [];
  const [isOpen, setIsOpen] = useState(false);

  if (features.length === 0) return null;

  return (
    <section className="card-header-specification  py-6 px-4 bg-gray-50 -mx-4 sm:-mx-0 sm:rounded-2xl">
      <div className="max-w-4xl mx-auto">
        {/* Compact header - always visible */}
        <button
          onClick={() => setIsOpen(!isOpen)}
          className="w-full flex items-center justify-between py-3 -mx-4 px-4 sm:mx-0 sm:px-0 sm:cursor-default"
        >
          <h2 className="text-xl font-bold text-gray-900">
            Specifications ({features.length})
          </h2>
          <div className="flex items-center gap-2">
            <span className="text-sm font-medium text-emerald-600 sm:hidden">
              {isOpen ? 'Hide' : 'Show all'}
            </span>
            {isOpen ? (
              <ChevronUp className="w-5 h-5 text-gray-600" />
            ) : (
              <ChevronDown className="w-5 h-5 text-gray-600" />
            )}
          </div>
        </button>

        {/* Ultra-compact list when closed - shows only first 4 */}
        {!isOpen && (
          <div className="space-y-3 mt-4">
            {features.slice(0, 4).map((item, i) => (
              <div key={i} className="flex items-center gap-3 py-2">
                <Check className="w-5 h-5 text-emerald-600 flex-shrink-0" />
                <span className="text-gray-700 text-sm">
                  <strong className="font-large">{item.label}:</strong> {item.value}
                </span>
              </div>
            ))}
            {features.length > 4 && (
              <div className="pt-2 text-center">
                <span className="text-sm text-gray-500">
                  + {features.length - 4} more specifications
                </span>
              </div>
            )}
          </div>
        )}

        {/* Full list when opened */}
        {isOpen && (
          <div className="mt-4 space-y-3 animate-in slide-in-from-top-2 duration-300">
            {features.map((item, i) => (
              <div key={i} className="flex items-start gap-3 py-2.5 border-b border-gray-100 last:border-0">
                <Check className="w-5 h-5 text-emerald-600 mt-0.5 flex-shrink-0" />
                <div className="flex-1">
                  <span className="font-medium text-gray-900">{item.label}</span>
                  <p className="text-gray-700 text-sm mt-0.5">{item.value}</p>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </section>
  );
}